import React, { useState, useRef } from "react";
import styled from "styled-components";
import emailjs from "@emailjs/browser";

const Cart = ({ cart, setCart }) => {
  const [showForm, setShowForm] = useState(false); // State  kim sot vic hin th form
  const [showCartList, setShowCartList] = useState(true); // State  kim sot vic hin th danh sch sn phm trong gi hng
  const [selectedProductInfo, setSelectedProductInfo] = useState(null); // Bin  lu thng tin sn phm c chn
  const [orderSuccess, setOrderSuccess] = useState(false); // State  kim sot vic hin th thng bo t hng thnh cng

  const handleRemoveFromCart = (productId) => {
    const updatedCart = cart.filter((product) => product._id !== productId);
    setCart(updatedCart);
  };

  const handleThanhToanClick = (selectedProduct) => {
    // Xo sn phm khi gi hng khi click vo nt "Thanh ton"
    handleRemoveFromCart(selectedProduct._id);
    // Hin th form
    setShowForm(true);
    // n danh sch sn phm trong gi hng
    setShowCartList(false);
    // Lu thng tin ca sn phm c chn
    setSelectedProductInfo(selectedProduct);
  };

  const handleExitButtonClick = () => {
    // n form khi click vo nt "Thot"
    setShowForm(false);
    // Hin th danh sch sn phm trong gi hng
    setShowCartList(true);
  };

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    // Ly gi tr email t trng input ca ngi dng
    const userEmail = form.current.user_email.value;

    // To ni dung email
    const messageContent = `Tn sn phm: ${
      selectedProductInfo.title || selectedProductInfo.name
    }\n
                             M t: ${selectedProductInfo.overview}\n
                             Gi: ${selectedProductInfo.price}`;

    emailjs
      .sendForm("service_qm2224r", "template_g508xbg", form.current, {
        to_email: userEmail, // S dng a ch email ca ngi dng
        publicKey: "QvkxCLR2QEfTosGdc",
        message: messageContent, // Thm thng tin sn phm vo ni dung email
      })
      .then(
        () => {
          console.log("SUCCESS!");
          // Hin th thng bo t hng thnh cng
          setOrderSuccess(true);
          // n thng bo sau 1 giy
          setTimeout(() => {
            setOrderSuccess(false);
          }, 1000);
        },
        (error) => {
          console.log("FAILED...", error.text);
        }
      );
  };

  return (
    <CartCss>
      {showForm && ( // S dng iu kin render  hin th form
        <StyledContactForm>
          <form ref={form} onSubmit={sendEmail}>
            <label>Tn ca bn</label>
            <input type="text" name="user_name" />
            <label>Email ca bn</label>
            <input type="email" name="user_email" />
            <label>Thng tin n hng</label>
            <textarea
              name="message"
              value={
                selectedProductInfo
                  ? `Tn sn phm: ${
                      selectedProductInfo.title || selectedProductInfo.name
                    }\nM t: ${selectedProductInfo.overview}\nGi: ${
                      selectedProductInfo.price
                    }`
                  : ""
              }
              readOnly
            />{" "}
            {/* in thng tin sn phm vo  */}
            <input type="submit" value="t Hng" />
            <input
              type="button"
              value="Thot"
              onClick={handleExitButtonClick}
            />{" "}
            {/* X l s kin khi click vo nt "Thot" */}
          </form>
        </StyledContactForm>
      )}
      {showCartList && ( // S dng iu kin render  hin th danh sch sn phm
        <div className="container-fluid">
          <div className="card">
            <div className="card-body">
              <h3>Danh sch sn phm trong gi hng</h3>
              <div className="row">
                {Array.isArray(cart) &&
                  cart.map((selectedProduct) => (
                    <div key={selectedProduct._id} className="col-md-4">
                      <h5 className="card-title fw-semibold mb-4">
                        {selectedProduct.title || selectedProduct.name}
                      </h5>
                      <div className="card">
                        <img
                          src={
                            selectedProduct.poster_path ||
                            "../assets/images/products/s4.jpg"
                          }
                          className="card-img-top"
                          alt="..."
                        />
                        <div className="card-body">
                          <p className="card-text">
                            {selectedProduct.overview}
                          </p>
                          <button
                            className="btn btn-primary"
                            onClick={() =>
                              handleThanhToanClick(selectedProduct)
                            }>
                            Thanh ton
                          </button>
                          <button
                            className="btn btn-danger"
                            onClick={() =>
                              handleRemoveFromCart(selectedProduct._id)
                            }>
                            Xa
                          </button>
                        </div>
                      </div>
                    </div>
                  ))}
              </div>
            </div>
          </div>
        </div>
      )}
      {orderSuccess && (
        <div className="alert alert-success" role="alert">
          t hng thnh cng!
        </div>
      )}
    </CartCss>
  );
};

export default Cart;

const CartCss = styled.div`
  .card-body h3 {
    margin-top: 56px;
  }
`;

const StyledContactForm = styled.div`
  width: 400px;
  margin: 100px auto;
  display: block; /* Hin th form khi cn */
  form {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    font-size: 16px;

    input {
      width: 100%;
      height: 35px;
      padding: 7px;
      outline: none;
      border-radius: 5px;
      border: 1px solid rgb(220, 220, 220);

      &:focus {
        border: 2px solid rgba(0, 206, 158, 1);
      }
    }

    textarea {
      max-width: 100%;
      min-width: 100%;
      width: 100%;
      max-height: 100px;
      min-height: 100px;
      padding: 7px;
      outline: none;
      border-radius: 5px;
      border: 1px solid rgb(220, 220, 220);

      &:focus {
        border: 2px solid rgba(0, 206, 158, 1);
      }
    }

    label {
      margin-top: 1rem;
    }

    input[type="submit"] {
      margin-top: 2rem;
      cursor: pointer;
      background: rgb(249, 105, 14);
      color: white;
      border: none;
    }
  }
`;
